export var isFunction = function isFunction(item) {
    return typeof item === 'function';
};
export var isNumber = function isNumber(item) {
    return typeof item === 'number';
};
export var isString = function isString(item) {
    return typeof item === 'string';
};
export var isArray = function isArray(item) {
    return item.constructor === Array;
};
export var isEmptyString = function isEmptyString(item) {
    return isString(item) && !item;
};

export var assignFunction = function assignFunction(item) {
    if (isFunction(item)) {
        return item;
    }
};

export var assignNumber = function assignNumber(number, defaultNumber) {
    if (isNumber(number)) {
        if (number === 0) {
            return Infinity;
        }

        return number;
    }

    return defaultNumber;
};